<template>
  <router-link :to="{ name: 'news', params: { slug: getSlug() } }" class="news-article">
    <div class="image" :style="{ backgroundImage: backgroundImage }"></div>
    <div class="summary">
      <div class="title">
        {{ articleTitle }}
      </div>
      <div class="details">
        <div class="source">
          From {{ article.source }}
        </div>
        <div class="date">
          {{ article.date | moment('MMMM Do') }}
        </div>
      </div>
    </div>
  </router-link>
</template>

<script>
import slugify from 'slugify'

export default {
  name: 'NewsArticle',
  props: {
    article: {
      type: Object,
      default: () => ({
        image: null,
        title: null,
        slug: null,
        source: null,
        summary: null,
        author: null,
        date: null,
        articleUrl: '#'
      })
    }
  },
  computed: {
    getUrl () {
      return (this.article.articleUrl) ? this.article.articleUrl : '#'
    },
    backgroundImage () {
      return `url('${this.article.imageUrl}')`
    },
    articleTitle () {
      const max = 90
      let title = (this.article.title)
        ? this.article.title
        : 'No Title Provided'

      if (title.length <= max) {
        return title
      }

      var subString = title.substr(0, max - 1)

      return subString.substr(0, subString.lastIndexOf(' ')) + ' ...'
    }
  },
  methods: {
    feedClicked () {
      this.$emit('feedClicked')
    },
    actionCenterClicked () {
      this.$emit('actionCenterClicked')
    },
    myImpactClicked () {
      this.$emit('myImpactClicked')
      this.$emit('hideDetails')
    },
    getSlug () {
      return (this.article.title)
        ? slugify(this.article.title, { replacement: '-', lower: true })
        : this.article.id
    }
  }
}
</script>

