    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    float: left;
  }

  .summary {
    display: inline-block;
    width: calc(100% - 65px);
    height: 65px;
    float: left;
    padding-left: 8px;
    position: relative;

    .title {
      font-weight: 900;
      line-height: 16px;
    }
  }

  .details {
    font-size: 10px;
    text-transform: uppercase;
    line-height: 10px;
    color: #666;
    position: absolute;
    bottom: 0;
    width: 100%;

    .date {
      position: absolute;
      right: 0;
      bottom: 0;
      color: #999;
    }
  }
}

@media only screen and (max-width: 320px) {
  .news-article {
    .summary {
      font-size: 14px;
    }
  }
}
</style>
